import React from "react"
import Layout from "globalLayoutsIndexPage"
import styled from "styled-components"
import { MDXRenderer } from "gatsby-plugin-mdx"
import { graphql } from "gatsby"
import Seo from "globalAtoms/Seo"
import getPageData from "globalLibs/helpers/get-page-data"

const PageStyle = styled.div`
  margin-top: -48px;
  @media (max-width: 576px) {
    margin-top: -64px;
  }
`

export default function DocumentationSinglePage(props) {
  const pageData = getPageData(props)

  return (
    <>
      <Seo pageData={pageData} ogType="article" />
      <Layout>
        <PageStyle
          as="article"
          className="container-fluid mt-48"
          style={{ maxWidth: "992px" }}
        >
          <h1 className="large-h1 mb-16">{pageData.frontmatter.title}</h1>
          <hr className="mt-0"></hr>
          <MDXRenderer>{pageData.body}</MDXRenderer>
        </PageStyle>
      </Layout>
    </>
  )
}

export const query = graphql`
  query ($slug: String!) {
    mdx(slug: { eq: $slug }) {
      ...DocumentationSinglePage
    }
  }
`
